<template>
  <div id="app">
    <mt-header fixed title="nCov-2019 charts"></mt-header>
    <div id="mapdiv" ref="mapContainer"></div>
    <EverydayData />
  </div>
</template>

<script>
import mapboxgl from "mapbox-gl";
import EverydayData from "./components/EverydayData.vue";
export default {
  components: { EverydayData },
  name: "App",
  data() {
    return {
      // date: null,
      // confirmed: null,
      // suspected: null,
      // cured: null,
      // dead: null
    };
  },
  methods: {
    init() {
      //创建地图对象
      mapboxgl.accessToken =
        "pk.eyJ1Ijoiamltd2VpIiwiYSI6ImNqN3ZoczZhZjZiZm0zMm8yeW00b2VkdjkifQ.Q_wogxtnFE11PatzGRkHEA";
      var map = new mapboxgl.Map({
        container: this.$refs.mapContainer,
        style: "mapbox://styles/jimwei/ck4mcmd4l7ufr1cpmh965ht2a",
        zoom: 2,
        center: [106, 36]
      });
      // map.setCenter([114, 32]);

      this.$http
        .get(
          // "https://raw.githubusercontent.com/canghailan/Wuhan-2019-nCoV/master/Wuhan-2019-nCoV.json"
          "./static/test.json"
        )
        .then(function(res) {
          console.log(res.body);
          // console.log(this.filterByProvince(res.body));

          console.log(this.filterByChina(res.body));
          console.log(this.filterChinaToday(res.body));
        });
    },
    // 筛选中国的每日数据
    filterByChina(arr) {
      return arr.filter(
        item => item.countryCode === "CN" && item.province === ""
      );
    },

    // 筛选出中国最新（当天）的数据
    filterChinaToday(arr) {
      let cnArr = this.filterByChina(arr);
      return cnArr[cnArr.length - 1];
    },

    // 筛选出中国前一天的数据(便于计算增长/减少量)
    filterChinaLastday(arr) {
      let cnArr = this.filterByChina(arr);
      return cnArr[cnArr.length - 2];
    },

    // 筛选各个省的每日数据
    filterByProvince(arr) {
      return arr.filter(item => item.province !== "" && item.city === "");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style>
body {
  margin: 0;
}
#app {
  padding-top: 40px;
}
#mapdiv {
  width: 100%;
  height: 300px;
  /* background-color: aqua; */
}
</style>



