<template>
  <div id="data">
    <div class="title-div">
      <div class="title-content">
        <span class="title-name">全国概况</span>
        <span class="end-date">截至{{ date }}</span>
      </div>
    </div>
    <div class="info-card-div">
      <div class="item confirmed split">
        <span class="top-text">
          较昨日
          <span class="add color">{{confirmedAdd}}</span>
        </span>
        <p class="number color">{{confirmed}}</p>
        <span class="type">确诊</span>
      </div>
      <div class="item wellcure split">
        <span class="top-text">
          较昨日
          <span class="add color">{{curedAdd}}</span>
        </span>
        <p class="number color">{{cured}}</p>
        <span class="type">治愈</span>
      </div>
      <div class="item confirmed split">
        <span class="top-text">
          较昨日
          <span class="add color">{{suspectedAdd}}</span>
        </span>
        <p class="number color">{{suspected}}</p>
        <span class="type">疑似</span>
      </div>
      <div class="item cure">
        <span class="top-text">
          较昨日
          <span class="add color">{{deadAdd}}</span>
        </span>
        <p class="number color">{{dead}}</p>
        <span class="type">死亡</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      date: null,
      confirmed: null,
      suspected: null,
      cured: null,
      dead: null,
      confirmedAdd: null,
      suspectedAdd: null,
      curedAdd: null,
      deadAdd: null
    };
  },
  methods: {
    init() {
      this.$http
        .get(
          "https://raw.githubusercontent.com/canghailan/Wuhan-2019-nCoV/master/Wuhan-2019-nCoV.json"
          //   "./static/test.json"
        )
        .then(function(res) {
          console.log(res.body);
          console.log(this.filterByChina(res.body));
          console.log(this.filterChinaToday(res.body));

          let todayData = this.filterChinaToday(res.body);
          let lastdayData = this.filterChinaLastday(res.body);
          //今日
          this.date = todayData.date;
          this.confirmed = todayData.confirmed;
          this.suspected = todayData.suspected;
          this.cured = todayData.cured;
          this.dead = todayData.dead;
          //较昨日
          this.confirmedAdd = todayData.confirmed - lastdayData.confirmed;
          this.suspectedAdd = todayData.suspected - lastdayData.suspected;
          this.curedAdd = todayData.cured - lastdayData.cured;
          this.deadAdd = todayData.dead - lastdayData.dead;
        });
    },
    // 筛选中国的每日数据
    filterByChina(arr) {
      return arr.filter(
        item => item.countryCode === "CN" && item.province === ""
      );
    },

    // 筛选出中国最新（当天）的数据
    filterChinaToday(arr) {
      let cnArr = this.filterByChina(arr);
      return cnArr[cnArr.length - 1];
    },

    // 筛选出中国前一天的数据(便于计算增长/减少量)
    filterChinaLastday(arr) {
      let cnArr = this.filterByChina(arr);
      return cnArr[cnArr.length - 2];
    },

    // 筛选各个省的每日数据
    filterByProvince(arr) {
      return arr.filter(item => item.province !== "" && item.city === "");
    }
  },
  mounted() {
    this.init();
  }
};
</script>
<style scope>
/* #data {
  width: 100%;
  height: 100px;
  background-color: aqua;
} */
.title-div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 1.7rem;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding-left: 0.26rem;
  border-bottom: 1px solid #f6f6f6;
}
.title-div .title-name {
  font-size: 15px;
  font-weight: 600;
  padding: 0.1rem;
}
.title-div .end-date {
  font-size: 12px;
  color: #999;
  font-weight: 300;
}
.info-card-div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.16rem 0;
  width: 100%;
  margin: 0 auto;
}
.info-card-div .item {
  position: relative;
  width: 33%;
  text-align: center;
}
.info-card-div .item .top-text {
  color: #979797;
  font-size: 12px;
}
.info-card-div .confirmed .color {
  color: red;
}
.info-card-div .item .type {
  font-size: 14px;
  color: #000;
}
.info-card-div .wellcure .color {
  color: #00bbc8;
}
.info-card-div .cure .color {
  color: #ffa900;
}
</style>


